@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@300;400&display=swap');

*{
  font-family: 'Barlow', sans-serif;
  box-sizing: border-box;
  margin: 0%;
  padding: 0%;
}

body{
  background: #c5c5c5;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

#header{
  font-family: 'Rubik', sans-serif;
}
#data{
  height: 450px;
  border: 1px solid black;
  width: 1100px;
  font-weight: 300;
  overflow-y: scroll;
}
.dataCustom{
  border: 1px solid black;
  height: 52px;
  width: 1100px;
  background-color: rgb(20, 20, 20);
  color: #e6e3e3;
  padding: 12px 0px 12px 12px;
  font-size: 1.2rem;
}

.death , .good , .active , .st , .orange , .purple{
  transition: 0.2s;
}
.death:hover{
  background-color: rgba(255, 52, 52, 0.788);
}
.good:hover{
  background-color: rgba(21, 255, 0, 0.753);
}
.active:hover{
  background-color: rgba(58, 58, 58, 0.521);
}
.st:hover{
  background-color: rgba(45, 177, 253, 0.836);
}
.orange:hover{
  background-color: rgba(253, 156, 45, 0.836);
}
.purple:hover{
  background-color: rgba(45, 253, 156, 0.836);
}

#conf{
  margin-left: 24rem;
}
#rec{
  margin-left: 2rem;
}
#dea{
  margin-left: 2rem;
}
#act{
  margin-left: 2.3rem;
}
#upd{
  margin-left: 2.2rem;
}